<template>
  <el-dialog
    :title="!dataForm.id ? '函数新增' : '函数修改'"
    :close-on-click-modal="false"
    top="80px"
    :visible.sync="visible">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="100px">
      <el-form-item label="函数名" prop="funCode">
        <el-input v-model="dataForm.funCode"></el-input>
      </el-form-item>
      <el-form-item label="函数名称" prop="funName">
        <el-input v-model="dataForm.funName"></el-input>
      </el-form-item>
      <el-form-item label="函数类型" prop="funType">
        <el-select v-model="dataForm.funType">
          <el-option v-for="item in funType" :label="item.text" :key='item.id' :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="函数状态" prop="funStatus">
        <el-radio-group v-model="dataForm.funStatus">
          <el-radio :label="0">停用</el-radio>
          <el-radio :label="1">启用</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="函数脚本" prop="funSql">
        <el-input type="textarea" :rows="5" v-model="dataForm.funSql"></el-input>
      </el-form-item>
      <el-form-item label="函数说明" prop="funDesc">
        <el-input type="textarea" :rows="5" v-model="dataForm.funDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        sysId:'',
        dataForm: {funId:0,funCode:'',funName:'',funType:0,funStatus:1,funSql:'',funDesc:''},
        dataRule: {
          funCode: [
            { required: true, message: '函数名必填', trigger: 'blur' }
          ],
          funName: [
            { required: true, message: '函数名称必填', trigger: 'blur' }
          ]
        },
        funType:[
          {id:0,text:'通用函数'},
          {id:1,text:'计算列函数'},
          {id:2,text:'校验函数'}
        ]
      }
    },
    methods: {
      init(id) {
        this.dataForm.funId=id || 0
        this.visible = true;
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(this.dataForm.funId) {
            this.$http({
              url: this.$http.adornUrl(this.API.queryDataFunctionById),
              method: 'post',
              params: this.$http.adornParams({
                funId:id
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.dataForm.funId=data.data.funId;
                this.dataForm.funCode=data.data.funCode;
                this.dataForm.funName=data.data.funName;
                this.dataForm.funType=data.data.funType?parseInt(data.data.funType):0;
                this.dataForm.funStatus=data.data.funStatus?parseInt(data.data.funStatus):1;
                this.dataForm.funSql=data.data.funSql;
                this.dataForm.funDesc=data.data.funDesc;
              }
            })
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.dataForm.funId=this.dataForm.funId || undefined
            this.$http({
              url: this.$http.adornUrl(this.API[!this.dataForm.funId?'addDataFunction':'updateDataFunction']),
              method:'post',
              data:this.$http.adornData(this.dataForm)
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            })
          }
        })
      }
    }
  }
</script>



